export function login(phoneNum: string, smsCode: string) {
    return fetchDataWithPost(GetLoginCustomDataURL, {
        mobl: phoneNum,
        code: smsCode,
    },false)
}

export function getRechargeList(userId: string) {
    return fetchDataWithPost(GetRechargeList,{
        userId: userId
    })
}

export function accountData(userId: string) {
    return fetchDataWithPost(GetAccountData,{
        userId: userId
    })
}

export function getUserList(userId: string) {
    return fetchDataWithPost(GetUsersList, {
        userId: userId
    })
}

export function getPullRefreshData(userId: string, page: string) {
    return fetchDataWithPost(GetPullRefreshData, {
        userId: userId,
        pageNum: page,
    },false)
}

//web
export function uploadFile(fileBase64: string, userId: string) {
    return fetchDataWithPost(UploadFileUrl, {
        fileBase64: fileBase64,
        userId: userId,
    })
}

//web
export async function uploadImageWithPost(filePath, name) {
    const userId = await window.GoldWeb.getUserId();
    const fetchPromise=await uploadFile(filePath,userId)
        .then((model)=>{
            return model
        }).catch((error)=>{
            throw error
        })

    return fetchPromise
}

//
export function uploadSupplement(userId: string,
                                 material_data: string,) {
    return fetchDataWithPost(SupplementUrl, {
        userId: userId,
        material_data: material_data,
    })
}





