//
export function getSendCode(mobile: string, validateKey: string) {
    return fetchDataWithDataString(GetSendCode, {
        mobile: mobile,
        validateKey: validateKey,
    }).then((model) => {
        if (model.msg) {
            Toast.info(model.msg, 3, null, false);
        }
        return model
    })
}

